import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import { HeaderButton, List, Subhead, Placeholder, Cell, Card, Link, platform, CellButton, FormStatus, Header, SimpleCell, InfoRow, View, FixedLayout, Search, Separator, Tabs, TabsItem, RichCell, Banner, Alert, Progress, Tooltip, IOS } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28HistoryForwardOutline from '@vkontakte/icons/dist/28/history_forward_outline';
import Icon44GiftOutline from '@vkontakte/icons/dist/44/gift_outline';
import {Icon56DonateOutline, Icon28DonateCircleFillYellow, Icon36FavoriteOutline, Icon56LockOutline} from "@vkontakte/icons";

import persik from '../img/persik.png';
import logo from '../img/logo.svg';
import android from '../img/android.svg';
import './Persik.css';
import './Tochka.css';



const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id} centered={true}>
				<PanelHeader
        left={<PanelHeaderButton onClick={go} data-to="home">
        {Home === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Задания MOBILE
		</PanelHeader>

                <Div style={{marginTop:80}}>
<Group header={<Header mode="secondary">Доступно для мобильных устройств</Header>}></Group>
<Card size="l" mode="outline">
            <div style={{ height: 10 }} />
            <Placeholder>
            <center>
            <Icon56DonateOutline width={45} height={45}/>
            </center>
              Специальные задания <br/><br/>
              <Button mode="outline" onClick={go} data-to="persik">Посмотреть</Button>
            </Placeholder>
          </Card>
          <br/>
          <center>
       <Subhead weight="bold" style={{ marginBottom: 16 }}>Доступные для всех</Subhead>
          </center>
      <Banner
        mode="image"
        header="Minecraft"
        subheader="Игра · 2 пункта" 
        background={
          <div
            style={{
              backgroundColor: '#65c063',
              backgroundImage: 'url(https://sun9-59.userapi.com/7J6qHkTa_P8VKRTO5gkh6MizcCEefz04Y0gDmA/y6dSjdtPU4U.jpg)',
              backgroundPosition: 'right bottom',
              backgroundSize: 320,
              backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={<Button disabled mode="overlay_primary" onClick={go} data-to="persik">Подробнее</Button>}
      />
            <Banner
        mode="image"
        header="PUBG MOBILE"
        subheader="Игра · 5 пунктов" 
        background={
          <div
            style={{
              backgroundColor: '#333240',
              backgroundImage: 'url(https://i.ibb.co/LthQ0zM/pubg.png)',
              backgroundPosition: 'right bottom',
              backgroundSize: 320,
              backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={<Button mode="overlay_primary" onClick={go} data-to="task_pubg">Подробнее</Button>}
      />

       <Banner
        mode="image"
        header="Monster Dash" 
        subheader="Игра · 3 пункта"
        background={
          <div
            style={{
              backgroundColor: '#4b4174',
              backgroundImage: 'url(https://ia.wampi.ru/2020/08/26/BEZ-IMENI-101c3e2692487c250.png)',
              backgroundPosition: 'right bottom',
              backgroundSize: 320,
              backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={<Button mode="overlay_primary" onClick={go} data-to="task_monsterdash">Подробнее</Button>}

      />

      <Banner
        mode="image"
        header="Hyper Jobs" 
        subheader="Игра · 6 пунктов"
        background={
          <div
            style={{
              backgroundColor: '#a655fe',
              backgroundImage: 'url(https://ia.wampi.ru/2020/08/30/BEZ-IMENI-1.png)',
              backgroundPosition: 'right bottom',
              backgroundSize: 320,
              backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={<Button mode="overlay_primary" onClick={go} data-to="task_hyperjobs">Подробнее</Button>}

      />

</Div>



	</Panel>



);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
