import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import { HeaderButton, List, Cell, Link, platform, CellButton, FormStatus, Header, SimpleCell, InfoRow, View, FixedLayout, Search, Separator, Tabs, TabsItem, Title, IOS } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28DocumentOutline from '@vkontakte/icons/dist/28/document_outline';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';


import persik from '../img/persik.png';
import logo from '../img/logo.svg';
import './Persik.css';
import './Tochka.css';

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id} centered={true}>
				<PanelHeader
			left={<PanelHeaderButton onClick={go} data-to="profile">
				{Home === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Верификация
		</PanelHeader>
                <Div className="payredinfo" style={{marginTop:15}}>
                  <FormStatus header="Верификация" mode="profile">
       При отправлении заявки на верификацию списывается сумма в 1 рубль, после оплаты Ваш профиль пройдет автоматическую проверку на подлинность страницы.
      </FormStatus>
</Div>
			<center>
     <Div>
     <Div style={{color: 'gray' }}>
           После оплаты Ваш профиль пройдет автоматическую проверку на подлинность страницы. Если Вас не оповестили об изменении статуса аккаунта в течении 12 часов, то просим Вас отправить скриншот чека об оплате нам в группу ВКонтакте.</Div>
    <br/>
    <h5>Заявка может находится в обработке до 24 часов.</h5>
     <center>
<Button before size="l" href="https://up.sale/pay/product/3cebf0cb">Завершить верификацию</Button>
       </center>
     </Div>
    		</center>

	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
